export default {
	props: {
		tabList: {
			type: Array ,
			default: function(e){
				return []
			}
		},
		tabIndex:{
			type:Number,
			default: 0
		},
		tabActiveIdx: {
			type: Number,
			default: 0
		},
		isScroll: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			// tabIndex:0,
			// scrollLeft: 0,
			scrollLeft: 0,
		};
	},
	watch: {
		tabActiveIdx(newValue, oldValue) {
			console.log('newValue', newValue);
			this.tabSelect(newValue,newCateid);
		}
	},
	methods: {
		tabSelect(newValue,newCateid) {
			var index  = newValue;
			// 具体的分类id
			var cateid =newCateid;
			this.scrollLeft = newValue * 30;
			
			this.$emit('tabSelect',index,cateid);
			// console.log( cateid,'id')
		}
	}
};
